import { createNamespacedHelpers } from 'vuex';
const { mapState, mapMutations } = createNamespacedHelpers('storeCommodity');
export default {
	data(){
		return {
			originalPassword:"",
			salesman:false,
			username:"",
			isBtn:false,
			btnTxt:"获取验证码",
			password:"",
			confirmPassword:"",
			code:"",
			...mapState(['isLogin'])
		}
	},
	onLoad(e){if(e.phone){this.username=e.phone;}},
	onShow(){
		// 获取用户信息
		this.$request.GET({
			url:this.$api.apiUrl.GET_USER_INFO
		}).then(res=>{
			let result = res.data;
			if(result.rule !== 'salesman'){
				// 普通用户
				this.salesman = false;
			}else{
				// 员工页面
				this.salesman = true;
			}
		})
	},
	methods:{
		...mapMutations(['LOGIN']),
		getCode(){
			var myreg = /^(((13[0-9]{1})|(14[0-9]{1})|(17[0-9]{1})|(15[0-3]{1})|(15[5-9]{1})|(18[0-9]{1})|(19[0-9]{1}))+\d{8})$/;
			// 判断手机号是否正确
			if(myreg.test(this.username)){
				this.$request.POST({
					url:this.$api.apiUrl.POST_SMS_GETCODE,
					data:{
						phone:this.username
					}
				}).then(res=>{
					if(res.errCode === 200){
						uni.showToast({
							title:res.errMsg
						})
						this.codeDisabled();
					}else{
						this.$api.msg(res.errMsg)
					}
				})
			}else{
				this.$api.msg("输入正确的手机号");return false
			}
		},
		//设置请求验证码倒计时
		codeDisabled(){
			// 按钮禁用
			this.isBtn = true;
			// 倒计时事件
			let time = 60;
			// 设置请求验证码倒计时
			let timeInterval = setInterval(()=>{
				if(time>0){
					this.btnTxt = (time--) + "秒后"
				}else{
					this.isBtn=false;
					this.btnTxt = "重新发送"
					time = 60;
					clearInterval(timeInterval);
				}
			},1000)
		},
		// 两次密码必须相同
		passwordIdentical(){
			var myregs = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
			if(!myregs.test(this.password)){this.$api.msg("密码需在6-16位数字和字母的组合"); return false;}
			if(this.password!==this.confirmPassword){
				this.$api.msg("你输入的密码不相同,请核实")
				return false;
			}
			return true
		},
		submit(){
			const isPssword = this.passwordIdentical();
			if(isPssword){
				this.$request.POST({
					url:this.$api.apiUrl.POST_SMS_CHECK,
					data:{
						code:this.code,
						phone:this.username
					}
				}).then(res=>{
					if(res.errCode === 200){
						this.$request.POST({
							url:this.$api.apiUrl.POST_USER_REPASS,
							data:{
								phone:this.username,
								password:this.password,
								repassword:this.confirmPassword
							}
						}).then(res=>{
							if(res.errCode === 1){
								uni.showToast({
									title:res.errMsg,
									success:()=>{
										this.userLogin();
									}
								})
							}else{
								this.$api.msg("更改失败,不能与当前使用密码相同")
							}
						})
					}else{
						this.$api.msg(res.errMsg)
					}
				})
			}
		},
		userLogin(){
			this.$request.POST({
				url:this.$api.apiUrl.POST_USER_LOGIN,
				data:{
					username:this.username,
					password:this.password
				}
			}).then(res => {
				if(res.code === 200){
					uni.setStorage({
						key: 'access_token',
						data: res.data,
						success:()=>{
							Promise.all([this.LOGIN({"isLogin":!this.isLogin()})]).then(() =>{
								uni.showToast({
									title: res.msg
								});
								uni.switchTab({
									url:"/pages/mys/user/user"
								});
							});		
						},
						fail:()=>{
							uni.showToast({
								title: "Token设置失败，请自行登录",
								success:()=>{
									uni.switchTab({
										url:"/pages/mys/login/login"
									});
								}
							});
						}
					});
				}else{
					this.$api.msg(res.msg);
				}
			})
		},
		staffSubmit(){
			// 判断是否输入原密码
			if(!this.originalPassword){this.$api.msg("请输入原密码");return false;};
			// 判断新输入的密码是否相同;
			const isPssword = this.passwordIdentical();
			if(isPssword){
				this.$request.POST({
					url:this.$api.apiUrl.POST_V6_PARTNER_CHANGEPASSWORD,
					data:{
						"oldpassword": this.originalPassword,
						"password": this.password,
						"repassword": this.confirmPassword
					}
				}).then(res=>{
					if(res.code === 'SECCUSS'){
						uni.showToast({
							title:res.msg,
							success:()=>{
								uni.switchTab({
									url:"/pages/mys/user/user"
								})
							}
						})
					}else{
						this.$api.msg(res.msg)
					}
				})
			}
		}
	}
}
